import { Component, OnInit ,ElementRef} from '@angular/core';
import { Router } from '@angular/router';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { DomSanitizer } from '@angular/platform-browser';
import { HttpService } from '../../api/http.service'
@Component({
  selector: 'app-activity-next',
  templateUrl: './activity-next.page.html',
  styleUrls: ['./activity-next.page.scss'],
})
export class ActivityNextPage implements OnInit {
  bannerImage:any;
  title:any;
  description:any;
  ActivityAction:any;
  private _link : string;
  frameUrl:any;
  safeUrl:any;
  currentColor:any;
  titleval:any;
  constructor(public shared: HttpService,public sanitizer: DomSanitizer,private _element : ElementRef,public router: Router,private _browser : InAppBrowser) { 
    const data = this.router.getCurrentNavigation().extras.state.data
    this.bannerImage=data.banner_image
    this.currentColor='light'
    this.title=data.detail_title;
    this.description=data.description;
    this.ActivityAction=data.activity_action;
    this.frameUrl=data.iframe_url;
    this.titleval=data.title
   // this.getTrustedUrl(this.frameUrl);
    if (this.ActivityAction=='iframe'){
      let opts : string = "location=yes,clearcache=yes,hidespinner=no"
      this._browser.create(this.frameUrl, '_blank', opts);
    }
  }



  
  ngOnInit() {
    this._enableDynamicHyperlinks()
    
  }


  getTrustedUrl(url:any){ 
    this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
   }
  private _enableDynamicHyperlinks() : void
  {
     // Provide a minor delay to allow the HTML to be rendered and 'found'
     // within the view template
     setTimeout(() =>
     {
        // Query the DOM to find ALL occurrences of the <a> hyperlink tag
        const urls : any    = this._element.nativeElement.querySelectorAll('a');

        // Iterate through these
        urls.forEach((url) =>
        {
           // Listen for a click event on each hyperlink found
           url.addEventListener('click', (event) =>
           {
              // Retrieve the href value from the selected hyperlink
              
              this._link = event.target.href;

              // Log values to the console and open the link within the InAppBrowser plugin
              console.log('Name is: ' + event.target.innerText);
              console.log('Link is: ' + this._link);
              // alert(this._link);
              // alert(this._link.indexOf('tel'));
              if(this._link.indexOf('tel') > -1){

              }else{
                event.preventDefault();
                this._launchInAppBrowser(encodeURI(this._link));
              }
              
           }, false);
        });
     }, 1000);
  }


  private _launchInAppBrowser(link : string) : void
  {
    let opts : string = "location=yes,clearcache=yes,hidespinner=no"
    this._browser.create(link, '_blank', opts);
  }

  onLoad(){
    this.shared.hideLoading()
  }

  ionViewWillLeave(){
    this.shared.hideLoading()
  }

}
